<template>
  <div class="dont">
    <!-- <p class="id">{{ list_data.id }}</p> -->
    <div class="title">
      <h2
        v-bind:class="{ hidden: listTitle }"
        v-on:click="(listCrest = true), (listTitle = true), (listInput = false)"
      >
        {{ list_data.title }}
      </h2>
      <span
        v-on:click="$emit('remove-list', list_data.id)"
        v-bind:class="{ hidden: listCrest }"
        >&#10006;</span
      >
      <input
        type="text"
        v-bind:class="{ hidden: listInput }"
        v-model="title"
        v-on:blur="
          Change(), (listInput = true), (listCrest = false), (listTitle = false)
        "
        v-on:keypress.enter="
          (Change(), (listInput = true)),
            (listCrest = false),
            (listTitle = false)
        "
      />
    </div>
    <Card
      v-for="card in cards"
      v-bind:key="card.id"
      v-bind:card_data="card"
      v-on:remove-card="removeCard"
    />
    <!-- <a href="#"
      ><div class="addNew"><p>+ Добавить карточку</p></div></a
    > -->
    <AddCard v-on:add-card="createCard" />
  </div>
</template>
<script>
import Card from '@/components/Card.vue';
import AddCard from '@/components/AddCard.vue';

export default {
  name: 'List',
  props: {
    list_data: {
      type: Object,
      default() {
        return {};
      },
    },
    cards: Object,
  },
  components: {
    Card,
    AddCard,
  },
  data() {
    return {
      listInput: true,
      listTitle: false,
      listCrest: false,
      title: this.list_data.title,
    };
  },
  methods: {
    createCard(card) {
      this.cards.push(card);
      console.log(this.cards);
    },
    removeCard(id) {
      this.cards = this.cards.filter((t) => t.id !== id);
    },
    Change() {
      if (this.title !== '') {
        this.list_data.title = this.title;
        console.log(this.list_data.title);
      } else {
        this.title = this.list_data.title;
      }
    },
  },
};
</script>
