<template>
  <div
    v-on:mouseover="hoverCard = false"
    v-on:mouseout="hoverCard = true"
    class="main"
  >
    <div
      class="second"
      v-bind:class="{ hidden: second }"
      v-on:click="Focus(), (second = true), (input = false)"
    >
      <p>
        {{ card_data.title }}
      </p>
      <span
        v-bind:class="{ hidden: hoverCard }"
        v-on:click="$emit('remove-card', card_data.id)"
        >&#10006;</span
      >
    </div>
    <div class="third" v-bind:class="{ hidden: input }">
      <input
        type="text"
        value="card_data.title"
        v-model="title"
        v-on:blur="Change(), (input = true), (second = false)"
        v-on:keypress.enter="Change(), (input = true), (second = false)"
        ref="myInput"
      />
    </div>
  </div>
</template>
<script>
export default {
  name: 'Card',
  data() {
    return {
      hoverCard: true,
      second: false,
      input: true,
      title: this.card_data.title,
    };
  },
  props: {
    card_data: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  methods: {
    Change() {
      if (this.title !== '') {
        this.card_data.title = this.title;
        console.log(this.card_data.title);
      } else {
        this.title = this.card_data.title;
      }
    },
    Focus() {
      console.log('focus');
      this.$nextTick(() => {
        // this won't work because `this.$refs.typeBox` returns an array
        // this.$refs.typeBox.focus()

        // this one works perfectly
        this.$refs.myInput[0].focus();
      });
    },
  },
};
</script>
