<template>
  <div class="main">
    <div class="addNew" v-bind:class="{ hidden: addCard }">
      <!-- v-on:click="Create" -->
      <a href="#" v-on:click="(isHidden = false), (addCard = true)"
        ><p>+ Добавить карточку</p>
      </a>
    </div>

    <div class="area" v-bind:class="{ hidden: isHidden }">
      <div class="textarea">
        <textarea
          name=""
          id=""
          placeholder="Введите название карточки"
          v-model="title"
        ></textarea>
      </div>
      <div class="controls">
        <input type="submit" value="Добавить карточку" v-on:click="onSubmit" />
        <a href="#" v-on:click="(isHidden = true), (addCard = false)"
          >&times;</a
        >
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'AddCard',
  data() {
    return {
      title: '',
      isHidden: true,
      addCard: false,
    };
  },
  methods: {
    Create() {
      console.log('Create');
      const newCard = {
        id: Date.now(),
        title: this.title,
      };
      this.$emit('create', newCard);
    },
    onSubmit() {
      console.log('Submit', this.title);
      if (this.title.trim()) {
        const newCard = {
          id: Date.now(),
          title: this.title,
        };
        this.$emit('add-card', newCard);
        this.title = '';
      }
    },
  },
};
</script>
