<template>
  <div class="main">
    <div class="second" v-bind:class="{ hidden: addList }">
      <a href="#" v-on:click="(addList = true), (isHidden = false)">
        <p>+ Добавить список</p>
      </a>
    </div>
    <div class="area" v-bind:class="{ hidden: isHidden }">
      <div class="textarea">
        <form v-on:submit.prevent="onSubmit">
          <input
            type="text"
            id="listName"
            placeholder="Введите название списка"
            v-model="title"
          />
          <div class="controls">
            <input type="submit" value="Добавить список" />
            <a href="#" v-on:click="(isHidden = true), (addList = false)"
              >&times;</a
            >
          </div>
        </form>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'AddList',
  data() {
    return {
      addList: false,
      isHidden: true,
      title: '',
    };
  },
  methods: {
    onSubmit() {
      console.log('Submit', this.title);
      if (this.title.trim()) {
        const newList = {
          id: Date.now(),
          title: this.title,
          cards: [],
        };
        this.$emit('add-list', newList);
        this.title = '';
      }
    },
  },
};
</script>
