<template>
  <div class="wrapper">
    <div class="first-board-nav">
      <router-link to="/">
        <div class="home">
          <a href="#">Домой</a>
        </div>
      </router-link>
      <div class="name" href="#">
        <a
          href="#"
          v-bind:class="{ hidden: first }"
          v-on:click="(first = true), showInput()"
          ><h1>
            {{ BOARD.title }}
            <!-- {{ $route.params.board_id }} -->
          </h1></a
        >
        <span id="hide"></span
        ><input
          id="txt"
          type="hidden"
          v-on:blur="(first = false), hideInput(), renameBoard()"
          v-on:keypress.enter="hideInput(), (first = false), renameBoard()"
        />
      </div>
      <div class="delete_board">
        <router-link to="/">
          <a v-on:click="removeBoard()" style="padding: 5px">Удалить доску</a>
        </router-link>
      </div>
    </div>
    <div class="element">
      <List
        v-for="list in BOARD.lists"
        v-bind:key="list.title"
        v-bind:list_data="list"
        v-bind:cards="list.cards"
        v-on:remove-list="removeList"
      />
      <AddList v-on:add-list="addList" />
    </div>
  </div>
</template>

<script>
import List from '@/components/List.vue';
import AddList from '@/components/AddList.vue';
import { mapActions, mapGetters } from 'vuex';

export default {
  data() {
    return {
      first: false,
    };
  },
  name: 'Board',
  props: ['lists'],
  computed: {
    ...mapGetters(['BOARD', 'BOARDS']),
  },
  async mounted() {
    this.GET_BOARD(this.$route.params.board_id);
    // const hide = document.getElementById('hide');
    // const txt = document.getElementById('txt');
    // // txt.value = this.BOARD.title;
    // function resize() {
    //   hide.textContent = txt.value;
    //   txt.style.width = `${hide.offsetWidth + 6}px`;
    // }
    // resize();
    // txt.addEventListener('input', resize);
  },
  components: {
    List,
    AddList,
  },
  methods: {
    ...mapActions(['GET_BOARD', 'DELETE_BOARD', 'RENAME_BOARD']),
    removeBoard() {
      // this.lists = this.lists.filter((t) => t.id !== id);
      this.DELETE_BOARD(this.$route.params.board_id);
    },
    renameBoard() {
      // const txt = document.getElementById('txt');
      this.RENAME_BOARD(this.$route.params.board_id, { title: 'test123' });
    },
    addList(list) {
      this.lists.push(list);
      console.log(this.list);
    },
    showInput() {
      const hide = document.getElementById('hide');
      const txt = document.getElementById('txt');
      function resize() {
        hide.textContent = txt.value;
        txt.style.width = `${hide.offsetWidth + 6}px`;
      }
      txt.addEventListener('input', resize);
      txt.value = this.BOARD.title;
      resize();
      txt.type = 'text';
      txt.select();
    },
    hideInput() {
      const txt = document.getElementById('txt');
      txt.type = 'hidden';
    },
  },
};
</script>

